<template>
    <div class="basic-info"
         v-show="currentStudent">
        <!--,             -->
        <h3 class="title"> HR </h3>
        <i-form v-ref:form-inline
                :model="formInline"
                :rules="rulerInline"
                :label-width="80">
    
            <Form-item label="">
                <i-input :value="currentStudent ? currentStudent.name : ''"
                         readonly
                         placeholder=""></i-input>
            </Form-item>
    
            <Form-item label=""
                       prop="gender">
                <Radio-group :model.sync="formInline.gender">
                    <Radio value="male"></Radio>
                    <Radio value="female"></Radio>
                </Radio-group>
            </Form-item>
    
            <Form-item label=""
                       prop="birthday">
                <Date-picker type="date"
                             placeholder=""
                             :value.sync="formInline.birthday"
                             format="yyyy-MM-dd">
                </Date-picker>
            </Form-item>
    
            <Form-item label=""
                       prop="phone">
                <i-input :value.sync="formInline.phone"
                         placeholder=""></i-input>
            </Form-item>
    
            <Form-item label=""
                       prop="collage">
                <i-input :value.sync="formInline.collage"
                         placeholder=""></i-input>
            </Form-item>
            <Form-item label=""
                       prop="specity">
                <i-input :value.sync="formInline.specity"
                         placeholder=""></i-input>
            </Form-item>
    
            <Form-item label=""
                       prop="highest">
                <Radio-group :model.sync="formInline.highest">
                    <Radio value="undergraduate"></Radio>
                    <Radio value="graduate"></Radio>
                    <Radio value="doctor"></Radio>
                </Radio-group>
            </Form-item>
    
            <Form-item label=""
                       prop="introduction">
                <i-input type="textarea"
                         :value.sync="formInline.introduction"
                         :autosize="{minRows: 5,maxRows: 5}"></i-input>
            </Form-item>
    
            <form-item>
                <i-button type="primary"
                          @click="next('formInline')"
                          :style="{width:'25%'}">
                </i-button>
            </form-item>
    
        </i-form>
    
    </div>
</template>

<script>
import { setSignInModal, routerGo, completeBasicInfo, setCurrentActiveKey } from '../../vuex/actions.js'
export default {
    data() {
        return {
            rulerInline: {
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                birthday: [
                    { required: true, message: '', type: 'date', trigger: 'change' }
                ],
                phone: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                collage: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                specity: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                gender: [
                    { required: true, message: '', trigger: 'change' }
                ],
                introduction: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                highest: [
                    { required: true, message: '', trigger: 'change' }
                ]
            }
        }
    },
    computed: {
        formInline: function () {
            return {
                birthday: new Date(this.currentStudent ? this.currentStudent.birthday || "1995/08/10" : new Date().toLocaleDateString()),
                phone: this.currentStudent ? this.currentStudent.phone : '15860',
                collage: this.currentStudent ? this.currentStudent.collage : "",
                specity: this.currentStudent ? this.currentStudent.speciality : "",
                highest: this.currentStudent ? this.currentStudent.highest : 'undergraduate',
                gender: this.currentStudent ? this.currentStudent.gender : 'male',
                introduction: this.currentStudent ? this.currentStudent.introduction : "  "
            }
        }

    },
    methods: {
        next(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.completeBasicInfo({
                        email: this.currentStudent.email,
                        birthday: this.formInline.birthday.toLocaleDateString(),// 2017/4/18
                        name: this.currentStudent.name,
                        phone: this.formInline.phone,
                        collage: this.formInline.collage,
                        speciality: this.formInline.specity,
                        highest: this.formInline.highest,
                        gender: this.formInline.gender,
                        introduction: this.formInline.introduction
                    }).then(res => {
                        if (res.ok) {
                            // console.log('shit');
                            this.routerGo('/key-info');
                        } else {
                            this.$Message.error('');
                        }

                    }).catch(err => {
                        this.$Message.error('');
                    })
                } else {
                    this.$Message.error('!');
                }
            })

        }
    },
    created() {
        this.setCurrentActiveKey(4);
        console.log(this.currentStudent);
        if (!this.currentStudent) {
            this.setSignInModal(true);
        }
    },
    vuex: {
        getters: {
            currentStudent: state => state.session
        },
        actions: {
            routerGo,
            setSignInModal,
            completeBasicInfo,
            setCurrentActiveKey
        }
    }
}


</script>

<style lang="less">
.basic-info {
    margin: 0 auto;
    margin-top: 30px;
    width: 50%;
    min-width: 300px;
    >.title {
        margin-bottom: 20px;
        margin-left: 80px;
        padding-bottom: 15px;
        border-bottom: 1px solid #ccc;
        font-size: 24px;
        font-weight: 300;
    }
    .ivu-input {
        resize: none;
        text-indent: 8px;
    }
}

;
</style>


